import { UserService } from './../../services/user.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { PhysicallService } from './../../services/physicall.service';
import { Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-exercicio',
  templateUrl: './exercicio.page.html',
  styleUrls: ['./exercicio.page.scss'],
})
export class ExercicioPage implements OnInit {
  physicals;
  form: FormGroup
  constructor(
    private router: Router,
    private physicalSrv: PhysicallService,
    private userSrv: UserService
  ) { }

  ngOnInit() {
    this.loadExercises()
    this.initialForm()
  }

  nextPage(){
    this.userSrv.insertPhysicalUser(this.form.value).subscribe(()=>{
       this.router.navigate(['saude'])
    })
    
  }

  initialForm(){
    this.form = new FormGroup({
      exercises: new FormControl(null,[Validators.required])
    })
  }

  onBack(){
    this.router.navigate(['objetivo'])
  }

  loadExercises(){
    this.physicalSrv.getPhysicall().subscribe(data => {
      this.physicals = data
    })
       
  }
}
